/*************************************************************************************
 * BUTTONS
 *************************************************************************************/
@import "bootstrap/less/mixins.less";
@import "bootstrap/less/buttons.less";
@import "variables.less";

.btn-link-danger.btn-link {
  color: @brand-danger;
}

.btn-default.active {
    box-shadow: none;
    color: #444;
}

.btn {
    text-transform: uppercase;
    outline: none !important;
    border: 1px solid @gray-light;
    margin-right: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    padding: 7px 13px;
    box-shadow: none;

    .btn::first-line {
      margin-top: 0;
    }

    .icon {
        margin-right: 5px;
    }

    &.btn-xs {
        padding: 1px 5px;
        font-size: @font-size-small;
        line-height: @line-height-base;
        border-radius: 3px;
    }

    &.btn-sm {
        padding: 5px 10px;
        font-size: @font-size-small;
        line-height: @line-height-base;
        border-radius: 3px;
    }
}

.ev-btn(@color) {
    background: @color;
    &:hover {
        background: lighten(@color, 4%);
    }
    &:active {
        background: darken(@color, 2%);
    }
    &.btn-xs,
    &.btn-sm,
    &.btn-xs:hover,
    &.btn-xs:active,
    &.btn-sm:hover,
    &.btn-sm:active {
        box-shadow: none;
    }
}

.btn-default { .ev-btn(@gray-lighter); }
.btn-danger { .ev-btn(@brand-danger); }
.btn-info { .ev-btn(@brand-info); }
.btn-warning { .ev-btn(@brand-warning); }
.btn-primary { .ev-btn(@brand-primary); }
.btn-red {
    .ev-btn(@brand-red);
    color: #fff;
    &:hover {
        color: #fff;
    }
}

.btn-primary {
  border: none;
}

.btn-primary[disabled] {
    background-color: @gray;
}

.btn-default {
    background: transparent;
    color: @gray-dark;
    &:hover {
        box-shadow: none;
        border-color: @gray-light;
    }
    &:active {
        box-shadow: none;
        opacity: .6;
    }
    &:focus {
      background-color: @gray-lighter;
    }
}

.btn-disabled {
  .btn;
  .btn-default;

  color: @gray;

  .icon {
    color: @gray;
  }

  &:hover {
    color: @gray;
    background-color: @gray-lighter;

    .icon {
      color: @gray;
    }
  }

  .opacity(.65);
  .box-shadow(none);



  pointer-events: all;
  cursor: not-allowed;
}

.btn-link {
  text-transform: none;
  white-space: normal;
  border: none;
  margin: 0;
  padding: 0;
  text-align: left;
}

.btn-link:hover {
  text-decoration: none;
}

.dropdown {
    list-style: none;
    display: inline-block;
}

.open .dropdown-toggle.btn-default {
    background: lighten(@gray-lighter, 4%);
    border-color: @gray-light;
}

.dropdown-menu {
  cursor: pointer;
  border: 1px solid @gray-light;
}

// Grey light button without border nor underline
.btn-light {
  background-color: transparent;
  margin: 0;
  padding: 0 3px;
  color: @gray;
  border: none;
  &:hover {
    box-shadow: none;
    color: @gray-dark;
  }
  .icon {
    margin: 0;
  }
}
